<template>
  <div class="bg"></div>
  <section class="container">
    <!-- <div style="width: 50%; height: 70%"></div> -->
    <div class="contact-con">
      <v-form
        class="py-3 form"
        ref="form"
        v-model="valid"
        @submit.prevent="submitForm"
      >
        <div class="mb-4">
          <span>
            <v-icon class="mr-2">mdi-email-outline</v-icon>
             
          </span>
        </div>
        <v-row>
          <v-col cols="5" sm="6" md="4" style="padding-bottom: 3px">
            <v-text-field
              v-model="form.name"
              label=""
              placeholder=" ."
              hint="ex) "
              variant="outlined"
              class="text-field"
              :rules="[(v) => !!v || ' .']"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4" style="padding-bottom: 3px">
            <v-text-field
              v-model="form.born"
              label=""
              placeholder="yymmdd"
              hint="ex) 991223"
              variant="outlined"
              class="text-field"
              :rules="[
                (v) => !!v || ' .',
                (v) => (v && v.length > 5) || ' .',
              ]"
              @input="form.born = form.born.replace(/[^0-9]/g, '').slice(0, 6)"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="3" sm="4" md="4" style="padding-bottom: 3px">
            <v-btn-toggle
              v-model="form.sex"
              :value="form.sex"
              variant="outlined"
              required
              :rules="[(v) => !!v || '  ']"
              divided
            >
              <v-btn value="left"></v-btn>
              <v-btn value="center"></v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="7" style="padding-top: 3px">
            <v-text-field
              v-model="form.email"
              label=""
              required
              :rules="[
                (v) => !!v || ' .',
                (v) =>
                  (v && v.length <= 35) || ' 35  .',
                (v) =>
                  /.+@.+\..+/.test(v) || '   .',
              ]"
              placeholder="example@example.com"
              hint="ex) example@example.com"
              variant="outlined"
              type="email"
              class="text-field"
            ></v-text-field>
          </v-col>
          <v-col cols="5" style="padding-top: 3px">
            <v-text-field
              v-model="form.phone"
              placeholder="01012345678"
              hint="ex) 01012345678"
              label=""
              variant="outlined"
              required
              :rules="[
                (v) => !!v || ' .',
                (v) => (v && v.length > 10) || ' .',
              ]"
              @input="
                form.phone = form.phone.replace(/[^0-9]/g, '').slice(0, 11)
              "
              class="text-field"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" style="padding-bottom: 5px">
            <v-text-field
              v-model="form.url"
              placeholder="yotube or twich or aficatv, etc"
              hint="*          ,      ."
              label="   URL"
              variant="outlined"
              required
              :rules="[(v) => !!v || '   url .']"
              class="text-field"
            ></v-text-field>
          </v-col>
        </v-row>
        <div>
          <v-row>
            <v-col cols="6" style="padding-bottom: 3px">
              <v-text-field
                v-model="form.nick"
                placeholder=" "
                hint="ex) "
                label=" "
                variant="outlined"
                required
                :rules="[(v) => !!v || '  .']"
                class="text-field"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="form.channel_name"
                placeholder=" "
                hint="ex) "
                label="  "
                variant="outlined"
                :rules="[(v) => !!v || '  .']"
                class="text-field"
              ></v-text-field>
            </v-col>
          </v-row>
        </div>
        <v-row>
          <div class="mail-content">
            <v-col cols="12" class="pb-0">
              <v-text-field
                v-model="form.title"
                label=""
                class="text-field"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-textarea
                v-model="form.content"
                label=""
                variant="outlined"
                class="text-field"
              ></v-textarea>
            </v-col>
          </div>
        </v-row>

        <v-checkbox
          v-model="form.rule_check"
          color="secondary"
          label="     (*)"
        ></v-checkbox>

        <v-btn type="submit" class="mx-0 mt-4" color="primary">
          <v-icon left>mdi-send</v-icon>
          Submit
        </v-btn>
      </v-form>
    </div>
    <!-- <v-alert v-if="showMessage" type="success" dismissible class="mt-4">
       
    </v-alert> -->
  </section>
</template>

<script>
export default {
  name: "ConatactPage",
  data() {
    return {
      alignment: 1,
      form: {
        name: "",
        born: "",
        email: "",
        phone: "",
        sex: "",
        url: "",
        nick: "",
        channel_name: "",
        title: "",
        content: "",
        rule_check: false,
      },
      valid: false,
      mockdata: [],
    };
  },
  methods: {
    submitForm() {
      if (!this.valid) {
        if (!this.form.rule_check) {
          alert("     .");
        }

        alert("   .");
        this.showErrors = this.$refs.form.errors;

        return false;
      }

      const data = {};
      for (const prop in this.form) {
        data[prop] = this.form[prop];
      }
      this.mockdata.push(data);
      console.log(this.mockdata);
      // location.reload();

      this.resetForm();
      alert(" .");
    },
    resetForm() {
      // form  .
      this.form = [
        {
          name: "",
          born: "",
          email: "",
          message: "",
          sex: "",
          title: "",
          content: "",
          url: "",
          rule_check: false,
        },
      ];
    },
  },
};
</script>

<style lang="scss" scoped>
.primary {
  background-color: #3f51b5 !important;
  color: #fff !important;
}
.bg {
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
  background-attachment: fixed;
}
.container {
  margin: 0;
  height: 100vh;
  width: 80%;
  margin: auto;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact-con {
  background-color: white;
  position: relative;
  width: 50%;
  margin-top: 5%;
  padding: 16px;
  border-radius: 30px;
  .form {
    border-top: 1px solid #8d8181;
    border-bottom: 1px solid #8d8181;
  }
  .mail-content {
    width: 100%;
    height: 100%;
    ::v-deep .v-input__details {
      display: none !important;
    }
  }
}
.my-col-class {
  padding: 8px;
}
</style>
